<template>
  <div class="detail" v-if="detail">
    <h4>名字</h4>
    <p>{{detail.name}}</p>
    <h4>入库数量</h4>
    <p>{{detail.in}}</p>
    <h4>库存总量</h4>
    <p>{{detail.balance}}</p>
    <h4>库存余量</h4>
    <p>{{detail.stock}}</p>
    <h4>更新时间</h4>
    <p>{{detail.update_at | formatDate}}</p>
    <h4>创建时间</h4>
    <p>{{detail.create_at | formatDate}}</p>
  </div>
</template>
<script>
import axios from 'axios';
import API from '@/api';
export default {
  data(){
    return {
      detail:{}
    }
  },
  created () {
    API.getFruitById(this.$route.params.id).then(res => {
      console.log(res);
      this.detail = res.result
    })
  }
}
</script>




